.plan {
  position: relative; }
.shadow {
  height: 20%;
  width: 100%;
  background: linear-gradient(to bottom, white, #bbb9b9);
  position: relative;
  h1 {
    color: grey;
    font-size: 11.7vw;
    line-height: 11.7vh;
    letter-spacing: 5px;
    opacity: .3;
    pointer-events: none;
    transform: scaleY(1.6);
    transform-origin: top center;
    position: relative;
 }    // z-index: 1
