  //   top: 0
  //   left: 0
  //   z-index: 2
 }  //   background: rgba(0,0,0,1)
.plan-preview {
  width: 100%;
  height: 80%;
  background: url("../Sources/Images/dar-1.jpg") no-repeat center;
  background-size: cover;
  h1 {
    text-align: center;
    font-size: 4rem;
    transform: translateY(-.6em); }
  p {
    text-align: center;
    width: 45%;
    display: block;
    margin: 1em auto;
    line-height: 1.4;
    transform: translateY(-3em); } }

.open-preview {
  position: absolute;
  bottom: 2em;
  left: 50%;
  transform: translateX(-50%);
  background-color: $maincolor;
  color: white;
  text-decoration: none;
  padding: .3em 2em .2em;
  border-radius: .3em;
  letter-spacing: 10px;
  font-size: 2rem; }
